<template>
  <div class="main_navs">
    <Header></Header>
    <div class="container mb-4" v-if="is_response">
<!--      <div class="title_ans">-->
<!--        Тест на темперамент-->
<!--      </div>-->
      <div class="ans_card">
        <div class="row">
          <div class="col-xl-8">

            <div class="ans_title">{{an_res.title}}</div>
            <div class="ans_desc">
              {{an_res.description}}
            </div>

            <div class="link_group">
              <div>
                <a @click="getOnetest()" class="link_main decoration" href="#">
                  <div>
                    Testga qaytish
                  </div>
                </a>
              </div>
              <div>
                <router-link class="link_main_calk decoration" to="/start-test">
                  <div>
                    Boshqa testga kirish
                  </div>
                </router-link>
              </div>
            </div>
          </div>
          <div class="col-xl-4">
            <img :src="an_res.image_url" width="100%" class="ans_img" alt="">
          </div>
        </div>
      </div>
    </div>
    <div class="container" v-if="!is_response">

      <div class="on_test_title">Shахsiyat tеsti vа ishdаn qаnоаtlаnish</div>
      <div class="on-test">
        <div class="one_test_progress">
          <div class="on-test-count">
            Вопросы {{ sliderValue }}/{{ $store.state.test_one.length }}
          </div>
          <div class="on-test-range">
            <range-slider
                class="slider"
                min="0"
                :max="$store.state.test_one.length"
                step="1"
                v-model="sliderValue">
            </range-slider>
          </div>
          <div class="on-test-card">
            <div>
              <div>
                <div class="on-test-quiz">
                  {{ $store.state.test_one[sliderValue - 1].title }}
                </div>
                <div class="answers">
                  <label :for="`quiz_ans${ite.id}`" class="ans_id "

                         v-for="(ite,ind) in $store.state.test_one[sliderValue-1].choices" :key="ind">
                    <input @input="getAns(ite)"  type="radio" :id="`quiz_ans${ite.id}`" :v-model="ite.id"
                           :name="`input_name${$store.state.test_one[sliderValue-1].id}`">
<!--                    <div class="test_circle">-->
<!--                      <div class="test_circle_child"></div>-->
<!--                    </div>-->
                    <span class="ans_text">{{ ite.text }}</span>
                  </label>
                </div>
              </div>
              <div class="on-test-button">
                <button class="prev" @click="prevStart()">

                  <span>Odldingisi</span>

                </button>
                <button v-if="sliderValue==$store.state.test_one.length" class="next" @click="endTest()">
                  <span>Tugatish</span>
                </button>
                <button v-else class="next" @click="nextStart()">
                  <span>Keyngisi</span>
                </button>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
    <Footer></Footer>
  </div>
</template>
<script>
import Header from "../../components/Header";
import Footer from "../../components/Footer";
import RangeSlider from 'vue-range-slider'
// you probably need to import built-in style
import 'vue-range-slider/dist/vue-range-slider.css'

export default {
  components: {Footer, Header, RangeSlider},
  data() {
    return {
      sliderValue: 1,
      value: 0,
      activeIndex: 0,
      max: 12,
      isActive: false,
      userResponses: [],
      answer: [],
      activeAn:[],
      an_res:{},
      is_response:false
    }
  },
  mounted() {
    this.$store.dispatch('getQuizOne', this.$route.params.id)
    this.max = this.$store.state.test_one.length

  },
  methods: {
    getAns(item) {
      this.answer[this.sliderValue - 1] = {
        "question_id": this.$store.state.test_one[this.sliderValue - 1].id,
        "choice_id": item.id
      }

    },
    nextStart() {
      if (this.sliderValue < this.$store.state.test_one.length) {
        this.sliderValue++
      }
    },
    endTest() {
      this.$axiosDefault.post(`test-result/`,
          {
            "answers": this.answer,
            "test_id": this.$route.params.id
          }
          ,
          {
            headers: {
              'Authorization': `Bearer ${localStorage.getItem('reg_token')}`,
            }
          })
      .then(res=>{
        this.an_res=res.data.result
        this.is_response=true
      })
    },
    getOnetest(){
      this.is_response=false
      this.sliderValue=1
      this.answer=[]

    },
    prevStart() {
      if (this.sliderValue > 1) {
        this.sliderValue--
      }
    }
  }
}
</script>
